.inp {
  min-width: 415px;
  width: 100%;
  padding: 16px 32px;
  height: 56px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  opacity: 0.2;
  border: none;
  outline: none;
  transition: background 0.3s ease-in-out, border 0.3s ease-in-out;

  &:hover {
    background: gray;

    &::placeholder {
      color: var(--white);
    }
  }

  &:focus {
    outline: 2px solid var(--dark-primary);
  }

  &:active {
    outline: 2px solid var(--dark-primary);
  }
}

.inp-white {
  color: var(--dark-primary);
  background: var(--white);

  &::placeholder {
    color: var(--dark-primary);
    opacity: 0.2;
  }
}

.inp-primary {
  color: var(--dark-primary);
  background: #d5cece;
}

.inp-foot {
  &::placeholder {
    color: #fff;
    opacity: 2;
  }
}

@media (max-width: 768px) {
  .inp {
    min-width: 380px;
  }
}

@media (max-width: 500px) {
  .inp {
    padding: 16px 10px;
    min-width: 215px;
  }
}
